@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    font-family: 'Roboto', sans-serif;
}

section {
    /*position: fixed;*/
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.scene-container {
    height: 100%;
    /*pointer-events: none;*/
}

p{
    font-weight: 300;
    color: #232931;

}
.content-bar {
    /*position: relative;*/

    width: 100vw;
    min-height: 300px;
    bottom: 0;

    color: black;

    padding: 1em 0.5em 3em 0.5em;

    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 30px;
    z-index: 5;
}

.input-group {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    align-content: start;
}

.input-group h3 {
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 500;
    color: white;
    width: 100%;
    padding: 10px 0 10px 0;
    text-align: center;
    background-color: black;
    border-left: 0.5em solid white;
    border-right: 0.5em solid white;
    /*margin-bottom: 10px;*/
}

button {
    background: transparent;
    border: 3px solid black;
    height: 40px;
    width: 60%;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 1em;

    cursor: pointer;

    transition: all 0.3s ease;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


button:focus {
    box-shadow: 0 0 0 5px rgba(0, 0, 0, 0.1);
    background: rgba(0, 0, 0, 0.1);
    outline: 0;
}

button:hover {
    background: black;
    color: white;
}

button:active {
    background: black;
    color: white;
}

svg {
    height: 70%;
    fill: black;
    transition: all 0.3s ease;
}


button:hover svg {
    fill: white;
}

.preset-selector {
    width: 60%;

    padding: 10px 0 10px 0;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.preset-name {
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 5px;
    color: black;
    width: 100%;
    padding: 10px 0 10px 0;
    text-align: center;
}

@media only screen and (max-width: 1024px) {
    .content-bar{
        grid-template-columns: 1fr 1fr;
    }
}

@media only screen and (max-width: 650px) {
    .content-bar{
        grid-template-columns: 1fr;
    }
}
.scene {
    border: none;
    outline: none;
    background: white;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.scene:focus {
    outline: 0;
}

canvas:focus {
    outline: 0;
}

.container{
    width: 70%;
    position: relative;
    height: 40px;
    overflow: hidden;

}

.container input{
    width: 100%;
    height: 100%;
    background-color: inherit;
    color: inherit;
    padding-top: 15px;
    border: none;
    outline: none;
    font-size: .9em;
    font-weight: 400;
}

.label-name{
    position: absolute;
    bottom: 0;
    left: 0;
    height: 100%;
    width: 100%;
    pointer-events: none;
    border-bottom: 1px solid #999999;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 0.9em;
}

.label-name::after{
    content: "";
    position: absolute;
    left: 0;
    bottom: -1px;
    height: 100%;
    width: 100%;
    border-bottom: 2px solid black;
    transform: translateX(-101%);
    transition: all 0.3s ease;
}

.content{
    position: absolute;
    bottom: 3px;
    left: 0;
    transition: all 0.3s ease;
}

.container input:focus + .label-name .content,
.container input:valid + .label-name .content{
    transform: translateY(-120%);
    font-size: 0.8em;
}

.container input:focus +.label-name::after, .container input:valid + .label-name::after{
    transform: translateX(0%);
}
*{
    position: relative;
}

.middle{
    width: 70%;
}

.slider-value{
    text-transform: uppercase;
    font-weight: 400;
}

.slider-container .slider{

    position: relative;
    z-index: 2;
    -webkit-appearance: none;
    width: 100%;
    height: 5px;
    border-radius: 1px;
    outline: none;
    background-color: transparent;
}


.slider-container .bar .fill{
    display: block;
    width: 50%;
    height: 2px;
    background-color: #232931;
}

.slider-container .bar{
    position: absolute;
    z-index: 1;
    left: 0;
    top: 31px;
    width: 100%;
    height: 1px;
    border-radius: 2px;
    background-color: #999999;
}

.slider-container .slider::-webkit-slider-thumb{
    -webkit-appearance: none;
    width: 20px;
    height: 20px;
    background-color: #eeeeee;
    border-radius: 50%;
    cursor: pointer;
    outline: none;
    border:  2px black solid;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.1);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.slider-container .slider::-webkit-slider-thumb:hover{
    background-color: black;
    box-shadow: 0 0 0 7px rgba(0, 0, 0, 0.1);
}

.slider-container .slider:active::-webkit-slider-thumb{
    background-color: black;
    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.25);
}

.slider-container .slider::-moz-range-thumb{
    -webkit-appearance: none;
    width: 17px;
    height: 17px;
    background-color: #eeeeee;
    border-radius: 50%;
    cursor: pointer;
    outline: none;
    border:  2px black solid;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.1);
    -moz-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.slider-container .slider::-moz-range-thumb:hover{
    background-color: black;
    box-shadow: 0 0 0 7px rgba(0, 0, 0, 0.1);
}

.slider-container .slider:active::-moz-range-thumb{
    background-color: black;
    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.25);
}

